.theme-header {
  background: url(https://ws4.sinaimg.cn/large/006tKfTcgy1g1om8efw2nj307z04a40f.jpg) no-repeat;
  background-size: 100% 100%;
  height: 20vh;
  filter: blur(2px);
}

.header-text {
  font-size: 3em;
  color: white;
  position: absolute;
  top: 5vh;
  left: 10px;
  right: 10px;
  text-align: center;

}

.cell {
  list-style: none;
  border-radius: 10px;
  margin: 5vw;
  padding: 5vw 5vw;
  background-color: white;
  box-shadow: 0 5px 10px 0 rgba(174, 174, 174, 0.4);
  display: flex;
  align-items: center;
  .avatar-name{
    .name{
      margin-left: 2vw;
    }
  }
  .work_name{
    font-size: large;
  }
  .work_money {
    margin-right: 1vw;
    font-size: 20px;
    font-weight: bold;
    // display: inline-block;
    padding: 3px 10px;
    border-radius: 50px;
    color: rgba(201, 160, 47, 0.9);
    background-color: rgba(255, 231, 166, 0.9);
  }
}
